import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import Image from "../components/image"
import SEO from "../components/seo"
import '../styles/pages/index.scss';

import { experiences, personalProjects, educations, openSourceContributes } from '../resumeData';

const workStartDate = new Date(2015, 1);

const getWorkPeriod = (startDate, endDate) => {
  endDate = endDate ? endDate : new Date()
  const workPeriod = endDate - startDate;
  const year = Math.floor(workPeriod / 3600 / 24 / 12 / 365)
  const month = Math.floor(workPeriod / 3600 / 24 / 12 / 30)
  let text = [];
  if (year) {
    text.push(`${year}년`)
  }
  if (month) {
    text.push(`${month}개월`)
  }
  return text.join(' ')
}
const getTotalPeriod = (experiences) => {

  let workPeriod = 0;
  for (let experience of experiences) {
    let startDate = experience.startDate
    let endDate = experience.endDate ? (experience.endDate * 1 + 108000000) : new Date()
    workPeriod += endDate - startDate;
  }

  const year = Math.floor(workPeriod / 31536000000)
  const month = Math.floor(workPeriod / 2592000000) % 12
  let text = [];
  if (year) {
    text.push(`${year}년`)
  }
  if (month) {
    text.push(`${month}개월`)
  }
  return text.join(' ')
}


const IndexPage = () => {
  const workPeriod = getWorkPeriod();
  return <Layout>
    <SEO title="ignocide resume" />
    <section id={"resume"}>
      <div className='resume-header'>
        <h1 className={'whoami'}>{'김성민'}<span className={'aka'}>{'(Sungmin Kim, a.k.a ignocide,gaeran)'}</span></h1>
        <h2 className={'job'}>{'Web Developer'}</h2>
        <p className='intoduce'>{'지금까지의 포지션에 구분을 두지 않는 환경에 근무하면서 백엔드와 프론트엔드에서 두루 역량을 쌓아왔습니다. 두루두루 할 수 있는 환경에 있었던 만큼 새로운걸 배우거나 마주하는 것에 대해 거부감이 없습니다. '}</p>
      </div>
      <div className='resume-personal-info'>
        <ul>
          <li><a href="mailto:ignocide@gmail.com">{'ignocide@gmail.com'}</a></li>
          <li><a href="//github.com/ignocide" target='_blank'>{'github.com/ignocide'}</a></li>
        </ul>
      </div>
      <div className={'resume-body'}>
        <div className={'part work-experiences'}>
          <h3>{'WORK EXPERIENCE'}<span className='work-period'>{getTotalPeriod(experiences)}</span></h3>
          {experiences.map((experience, index) => {
            return <WorkExperience key={experience.company + index} experience={experience} />
          })}
        </div>
        <div className={'part personal-projects'}>
          <h3>{'Personal Project'}</h3>
          {personalProjects.map((project) => {
            return <PersonalProject key={project.name} project={project} />
          })}
        </div>

        <div className={'part open-source-contributions'}>
          <h3>{'OpenSource Contributes'}</h3>
          {openSourceContributes.map((contribute) => {
            return <OepnSourceContributes key={contribute.name} contribute={contribute} />
          })}
        </div>

        <div className={'part educations'}>
          <h3>{'Educations'}</h3>
          {educations.map((education) => {
            return <Education key={education.name} education={education} />
          })}
        </div>

      </div>
    </section>
  </Layout>
}

const WorkExperience = ({ experience }) => {
  return <div className={'work-experience'}>
    <h4 className={'company'}>{experience.company}<DatePeriod start={experience.startDate} end={experience.endDate} /></h4>
    <div>
      <p className={'summary'}>{experience.summary}</p>
      <ul className={'projects'}>
        {experience.projects.map((project, index) => {
          return <li className={'project'} key={project.name}>
            <div className={'project-name'}>{project.name}</div>
            <p className={'project-summary'}>{project.summary}</p>
            <ul className='project-jobs'>
              {(project.works || []).map((work, index) => <li key={index}>{work}</li>)}
            </ul>
          </li>
        })}
      </ul>

      <div className='tech-stack'>
        {(experience.techStack || []).map((teck, index) => <span key={index} className={'tech'}>{teck}</span>)}
      </div>
    </div>
  </div>
}

const PersonalProject = ({ project }) => {
  return <div className={'personal-project'}>
    <h4 className={'project-name'}>{project.name}</h4>
    <p className={'project-description'}>{project.description}</p>
    <div className='tech-stack'>
      {(project.techStack || []).map((teck, index) => <span key={index} className={'tech'}>{teck}</span>)}
    </div>
  </div>
}

const Education = ({ education }) => {
  return <div className={'education'}>
    <h4 className={'education-name'}>{education.name}<DatePeriod start={education.startDate} end={education.endDate} /></h4>
    <p className={'education-description'}>{education.description}</p>
  </div>
}

const OepnSourceContributes = ({ contribute }) => {
  return <div className={'open-source-contribute'}>
    <h4 className={'open-source-contribute-name'}>{contribute.name}</h4>
    <p className={'open-source-contribute-description'}>{contribute.description}</p>
    <ul className={'open-source-contribute-works'}>{contribute.works.map((work) => {
      return <li className='pr-label'><a href={work.link} target={'_blank'}>{work.label}</a></li>
    })}</ul>
  </div>
}

const DatePeriod = ({ start, end }) => {
  let startDate = start ? `${start.getFullYear()}-${start.getMonth()}` : null
  let endDate = end ? `${end.getFullYear()}-${end.getMonth()}` : '재직 중'
  return <span className='date-period'>{`${startDate} ~ ${endDate}`}</span>
}

export default IndexPage
